@import url(https://fonts.googleapis.com/css2?family=Domine:wght@400;700&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.3.0/font/bootstrap-icons.css);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7ebf0;
}

* {
  font-family: 'Domine', serif;
}

div {
  color: black;
}

::-webkit-calendar-picker-indicator {
  filter: invert(1);
}
:root {
  --bar: #006db3;
  --surface: #0086c3;
  --bg: #e7ebf0;
  --secondaryBg: #1f2c34;
  --toastBg: #6d6c6c71;
  --green: #005c4b;
}

body {
  background-color: (--bg);  
  color: white;
  font-size: 4vw;
}

input {
  outline: 0;
  border-width: 0 0 0.1rem;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  font-size: 4vw;
}

button {
  background-color: #005c4b;
  background-color: var(--green);
  color: white;
  outline: none;
  border: none;
  cursor: pointer;
  font-size: 5vw;
  padding: 2%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: background-color 0.5s;
}

button:disabled {
  opacity: 0.5;
}

input:focus {
  border-color: #005c4b;
  border-color: var(--green)
}

i {
  color:white;
  font-size: 6vw;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0 2vw 0 .75vw;
}

.App {
  display: grid;
  grid-template-areas:
  'header'
  'body'
}

.glass {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 99;
}

.dark-glass {
  background-color: black;
  opacity: 0.75;
}

.clickable {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

span.ripple {
  position: absolute; /* The absolute position we mentioned earlier */
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 600ms linear;
  background-color: rgba(255, 255, 255, 0.7);
}

.unsel {
  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

.light {
  color: #555;
}

.pageRoot {
  max-height: 90vh;
  overflow: scroll;
}

.actionButton  {
  color: #005c4b;
  color: var(--green)
}
#login-form{
    display: grid;
}

#login-logo {
    width: 60%;
    padding-bottom: 20%;
}

#login-form {
    width: 80%;
    display: grid;
    grid-row-gap: 30%;
    row-gap: 30%;
    padding-bottom: 40%;
}

#login-root {
    height: 100vh;
    display: grid;
    align-items: center;
    justify-items: center;
    align-content: center;
}

.login-spinner {
    width: 100%;
    display: grid;
    justify-content: center;
}
.spinner-bg-circle {
    border: 3px solid rgba(128, 128, 128, 0.397);
    border-top: 3px solid var(--green);
    border-right: 3px solid var(--green);
    border-bottom: 3px solid var(--green);
    width: 15px;
    height: 15px;
    border-radius: 50%;
    animation: spinner 1s ease-in-out infinite;
    transition: opacity 0.5s;
    opacity: 1;
}

.spinner-show {
 opacity: 1;
}

.spinner-hide {
 opacity: 0;
}

@keyframes spinner {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

.toast {
  position: fixed;
  background-color: var(--toastBg);
  color: white;
  padding: 2% 5% 2% 5%;
  border-radius: 1000px;
  max-width: 80%;
  min-width: 30%;
  text-align: center;
  transition: opacity .5s ease-in-out;
  white-space: pre-wrap;
  overflow: hidden;
}

.toast-show {
  opacity: 1;
}

.toast-hide {
  opacity: 0;
}

.mainMenu {
    position: fixed;
    z-index: 100;
    left: 0;
    width: 70%;
    height: 100vh;
    background-color: var(--surface);
    transition: left .3s ease-in-out, opacity .3s ease-in-out;
    box-shadow: #0005 2px 2px 10px;
}

.mainMenuItem {
    color: white;
    padding: 3vw;
}

.mainMenu#show {
    left: 0px
}

.mainMenu#hide {
    left: -80vw;
}

.mainMenuProfile {
    color: white;
    display: grid;
    justify-items: center;
}

.mainMenuProfileAvatar {
    display: grid;
    background: cadetblue;
    font-size: 8vw;
    font-weight: 400;
    border-radius: 50%;
    width: 10vw;
    height: 10vw;
    align-content: center;
    justify-content: center;
    margin: 3vw;
}

.mainMenuProfileName {
    font-size: 5vw;
    font-weight: 400;
}



.header_logo {
    width:12vw;
}

.appHeader {
    position: relative;
    display: flex;
    height: 12vw;
    width: 100%;
    background-color: var(--bar);
    align-items: center;
    justify-content: space-between;
}

#menuButton {
    margin: 0% 3% 0% 0%;
    font-size: 4vw;
}

#slideMenuButton {
    margin: 0% 0% 0% 3%;
}

.clickableIcon {
    display: flex;
    width: 6vw;
    height: 6vw;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    border-radius: 50%;
}
.pageMenu {
    position: fixed;
    z-index: 100;
    right: 0;
    width: 50%;
    background-color: white;
    color: black;
    transform-origin: 100% 0%;
    transition: transform .3s ease-in-out, opacity .3s ease-in-out;
    box-shadow: #0005 2px 2px 10px;
}

.pageMenuItem {
    padding: 3vw;
}

.pageMenu#show {
    transform: scale(1);
    opacity: 1;
}

.pageMenu#hide {
    transform: scale(0);
    opacity: 0;
}





.inputAuto {
    position: absolute;
    width: 90%;
    margin: 0 5% 0 5%;
}

.titlebar_title {
    font-size: 4.5vw;
    color: white;
    margin-left: 2vw;
    font-weight: bold;
}

.titlebar {
    position: relative;
    display: flex;
    height: 10vw;
    width: 100%;
    background-color: var(--bar);
    align-items: center;
    justify-content: space-between;
}

.titlebarChildren {
    display: flex;
}

.titlebarChildren > *{
    margin-right: 2vw;
    display: flex;
    align-items: center;
}
#attendanceSearch {
    width: 90%;
    margin: 0 5% 0 5%;
}
.tab {
    color: white;
    width: 100vw;
    font-size: 3.7vw;
}

.tabHeaderItems {
    display: flex;
    justify-content: space-around;
    height: 9vw;
    align-items: center;
}

.tabHeaderItem {
    width: 100%;
    text-align: center;
    cursor: pointer;
}


.tabUnderline {
    position: relative;
    height: .4vw;
    background: var(--green);
    transition: left .3s ease-in-out;
}

.tabHeader {
    background-color: var(--secondaryBg);
}
.atcContact {
    border-bottom: 0.0001em #555 solid;
    display: flex;
    align-items: center;
}

.atcNamePhone {
    margin: 1vw 0vw 1vw 2vw;
    flex: 8 1;
}

.atcName {
    font-weight: 700;
    font-size: 3.8vw;
    margin-bottom: 1.25vw;
}

.atcPhone {
    font-size: 3.25vw;
    font-weight: 100;
}

.atcTick {
    font-size: 2.75vw;
    cursor: pointer;
    display: flex;
    flex: 1 1;
    flex-direction: column;
    padding: 4%;
    align-items: center;
}
.modalBox {
  position: fixed;
  top: 15vh;
  width: 80vw;
  z-index: 999;
  left: 10vw;
  background-color: #2a3b45;
}

.modalBody {
  background-color: var(--bg);
  padding: 3%;
  min-height: 50vw;
}

.modalTitle {
  padding: 2% 2% 2% 4%;
  background-color: var(--secondaryBg);
  min-height: 5vw;
}

.modalTitleItems{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.checkItem {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    padding: 1%;
    cursor: pointer;
}

.checkItem.selected {
    background-color: var(--secondaryBg);
}

.checkBox {
    display: flex;
    flex-direction: column;
}

.checkName {
    padding: 0 0 0 2%;
}
#contactList {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contactItem {
    margin: 2vw 0 2vw 0;
}

.contactItemHolder {
    display: flex;
    justify-content: space-between;
}
.paper {
    background-color: white;
    padding: 3vw;
    width: 80vw;
    border-radius: 1vw;
    box-shadow: -2px 2px 3px #aaa;
}
